import '@servicenow/now-tabs';
import '@servicenow/now-record-checkbox';
import '@devsnc/sn-record-input';
import 'sn-truncate-text';
import '@servicenow/now-button';
import { t } from 'sn-translate';

import './snCanvas/snCanvas';
import * as utils from './utils/methods';

export default (state, {dispatch, updateState}) => {

	const {
		type,
		ackText
	} = state.properties;

	const { checked } = state;

	//TODO: Remove this workaround, added to avoid tectonic infinite loop on typed signature update
	let signature = state.signature.hasOwnProperty(type) ? state.signature[type].data : state.properties.signature;
	//sanitize signature
	signature = utils.sanitizeSignature(signature, type);

	//dispatch signature information when set from properties
	if (signature && state.properties.signature && !state.signature.hasOwnProperty(type)) {
		type === 'type' ? utils.dispatchSignatureInfo(
			type,
			signature,
			signature ? utils.generateSignatureImage(signature) : '',
			dispatch,
			updateState
		) : '';
	}

	//tabs
	const tabs = [
		{
			id: 'type',
			label: t('Type signature')
		},
		{
			id: 'draw',
			label: t('Draw signature')
		}
	];

	//resize canvas for the available width
	const canvasWidth = state.behaviors.resize.size.width - 2;

	return (
		<div>
			{/* Acknowledgment text */}
			{ ackText ?
				<div className="ackText">
					<div className="ackCheckbox">
						<now-record-checkbox
							name="ack-checkbox"
							value= { checked }
						/>
					</div>
					<div>
						<sn-truncate-text
							text={ ackText }
						/>
					</div>
				</div>
			: '' }

			{/* Tab selector */}
			<now-tabs
				items={ tabs }
				selectedItem={ type }>
			</now-tabs>

			{/* Signature pad */}
			{
				type === 'draw' ?
					// Draw signature
					<div className = "signaturePadBox">
						<div className = "clearSigButton">
							<now-button-bare
								size = "md"
								label = { t('Clear') }
								variant = "secondary"
							/>
						</div>
						<div className = "signatureDrawPad">
							<sn-canvas
								height = '190'
								width = { canvasWidth }
								data = { signature }
							/>
						</div>
					</div>
				:
					// Type signature
					<div className="inputNameField">
						<sn-record-input
							label = { t('Print your name') }
							value = { signature }
							maxlength = "25"
						/>
					</div>
			}

			{/* Default signature pad text */}
			<div className="signatureText">
				{t('Filling in the following information will constitute your eSignature and will have the same legal impact as signing a printed version of this document.')}
			</div>
		</div>
	);
};
