import * as ACTIONS from './constants';

export function dispatchSignatureInfo(sigType, sigData, sigImage, dispatch, updateState) {

	//update internal state
	updateState({
		path: 'signature.'+sigType,
		operation: 'set',
		value: {
			data: sigData,
			image: sigImage
		}
	});

	//dispatch current signature data
	dispatch(ACTIONS.SIGNATURE_PAD_SIGNED, {
		signature: {
			type: sigType,
			data: sigData,
			image: sigImage
		}
	});
}

export function generateSignatureImage(signatureData) {
	const canvas = document.createElement('canvas');
	const canvasContext = canvas.getContext('2d');

	canvasContext.canvas.width = 752;
	canvasContext.canvas.height = 190;
	canvasContext.font = '60px cursive';
	canvasContext.fillText(signatureData, 10, 100);

	return canvasContext.canvas.toDataURL();
}

export function sanitizeSignature(signature, type) {

	if (signature) {
		if (type === 'type' && !(signature.length > 25))
			return signature;

		if (type === 'draw') {
			try {
				return JSON.parse(signature);
			} catch(e) {
				return '';
			}
		}
	}

	return '';
}
