import { createRef } from '@seismic/snabbdom-renderer';
import initDrawingCanvas from './canvasUtility';

export default ( state, { dispatch } ) => {

	const { height, width, data } = state.properties;
	const canvasRef = createRef();
	let canvasUtils;

	function initCanvas() {
		canvasUtils = initDrawingCanvas({ canvas: canvasRef.current });

		//regenerate image for data and dispatch information
		if (data && reconstructSignature())
			dispatchSignatureInfo();
		else
			canvasUtils.clear();
	}

	function reInitCanvas() {
		//re-instantiate canvas draw utils
		canvasUtils = initDrawingCanvas({ canvas: canvasRef.current });

		//if signature data render it (no need dispatch info again)
		if (data)
			reconstructSignature();
		else
			canvasUtils.clear();
	}

	function reconstructSignature() {
		//parse JSON signature data
		const dataType = typeof data;
		let signPath = dataType === 'object' ? data : dataType === 'string' ? JSON.parse(data) : '';

		//reconstruct signature
		if (signPath && signPath.length > 0) {
			canvasUtils.setPath(signPath);
			return true;
		} else
			return false;
	}

	function dispatchSignatureInfo() {
		dispatch('SN_DRAWING_CANVAS#CHANGED', {
			data: JSON.stringify(canvasUtils.getPath()),
			image: canvasUtils.toDataURL()
		})
	}

	return (
		<div>
			<canvas
				// create element reference to bind utility
				ref = { canvasRef }
				hook-insert = { initCanvas }
				hook-update = { reInitCanvas }

				// properties
				height = { height }
				width = { width }

				// dispatch image information on change
				onmouseup = { dispatchSignatureInfo } >
			</canvas>
		</div>
	);
};
