import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import resizeBehavior from '@servicenow/behavior-resize';

import view from './view.js';
import styles from './snSign.scss';
import actionHandlers from './actionHandlers';
import * as ACTIONS from './utils/constants';

export default createCustomElement('sn-sign', {

	styles,

	behaviors: [
		{
			behavior: resizeBehavior,
			options: {timer: {wait: 200}}
		},
	],

	renderer: {
		type: snabbdom,
		view
	},

	actionHandlers,

	actions: {
		[ACTIONS.TAB_SELECTED]: { private: true }
	},

	initialState: {
		checked: 'false',
		signature: ''
	},

	properties: {
		type: {
			default: 'type'
		},
		ackText: '',
		signature: ''
	}
});
