import * as ACTIONS from './utils/constants';
import * as utils from './utils/methods';

export default {

	//set property for internal view
	//dispatch signature for selected tab
	[ACTIONS.TAB_SELECTED]: ({ state: { signature }, dispatch, action: { payload: { value }}, updateProperties }) => {
		updateProperties({
			type: value,
			signature: ''
		});

		dispatch(ACTIONS.SIGNATURE_PAD_SIGNED, {
			'signature': {
				type: value,
				data: signature.hasOwnProperty(value) ? signature[value].data : '',
				image: signature.hasOwnProperty(value) ? signature[value].image : '',
			}
		});
	},

	[ACTIONS.CHECKBOX_CHECKED]: ({updateState, state, action}) => {
		const { value } = action.payload;
		updateState ({
			...state,
			checked: value
		});
	},

	[ACTIONS.INPUT_CHANGED]: ({ action: { payload: { value } }, dispatch, updateState }) => {
		utils.dispatchSignatureInfo(
			'type',
			value.trim(),
			value.trim() ? utils.generateSignatureImage(value) : '',
			dispatch,
			updateState
		);
	},

	[ACTIONS.BUTTON_CLICKED]: ({dispatch, updateState}) => {
		utils.dispatchSignatureInfo(
			'draw',
			'',
			'',
			dispatch,
			updateState
		);	
	},

	[ACTIONS.CANVAS_SIGNED]: ({ action: { payload }, dispatch, updateState }) => {
		utils.dispatchSignatureInfo(
			'draw',
			payload.data,
			payload.image,
			dispatch,
			updateState
		);
	}
};
