/**
 * Creates a dependency-free drawing surface that allows the user to draw
 * an image and the application to request that image as a base64 data URL.
 * Handles mouse, touch, and mouse+touch input devices. Works in all modern
 * browsers without polyfills.
 *
 * @param {Object} options
 * @param {HTMLCanvasElement} options.canvas - <canvas> element instance to draw to
 * @param {string} [options.strokeStyle="black"] - CSS style for the stroke
 * @param {number} [options.lineWidth=2] - Line width for the stroke
 * @returns {DrawingCanvas} - Drawing canvas instance with following methods:
 * 							clear, setPath, getPath, hasPath, toDataurl
 */
export default function initDrawingCanvas({
		canvas,
		strokeStyle = 'black',
		lineWidth = 2
	}) {
	const ctx = canvas.getContext('2d');

	const mouseEventToCoord = (canvas, inputDevice, e) => {
		const clientX = inputDevice === 'mouse' ? e.clientX : e.targetTouches[0].clientX;
		const clientY = inputDevice === 'mouse' ? e.clientY : e.targetTouches[0].clientY;
		const canvasBound = canvas.getBoundingClientRect();

		const lx = clientX - canvasBound.left;
		const ly = clientY - canvasBound.top;
		return [lx,ly];
	};

	const draw = (ctx, lx, ly, mx, my) => {
		if (typeof mx !== 'undefined' && typeof my !== 'undefined') {
			// user continued drawing, connect last coord to current coord
			ctx.beginPath();
			ctx.moveTo(mx, my);
			ctx.lineTo(lx, ly);
			ctx.strokeStyle = strokeStyle;
			ctx.lineWidth = lineWidth;
			ctx.stroke();
			ctx.closePath();
		} else {
			// user started drawing, just make a dot
			ctx.beginPath();
			ctx.fillStyle = strokeStyle;
			ctx.fillRect(lx, ly, lineWidth, lineWidth);
			ctx.closePath();
		}
	};

	let drawing = false;
	let signing = false;
	let lastCoord = null;
	let currentPath = [];

	const onDrawStart = (canvas, ctx, inputDevice, event) => {
		drawing = true;
		const [lx,ly] = mouseEventToCoord(canvas, inputDevice, event);
		draw(ctx, lx, ly);
		lastCoord = [lx,ly];
		currentPath.push({lx, ly});
	};

	const onDrawContinue = (canvas, ctx, inputDevice, event) => {
		if (!drawing) return;
		const [lx,ly] = mouseEventToCoord(canvas, inputDevice, event);
		const [mx, my] = lastCoord;
		draw(ctx, lx, ly, mx, my);
		lastCoord = [lx,ly];
		currentPath.push({lx, ly, mx, my});
	};

	const onDrawEnd = () => {
		drawing = false;
		lastCoord = null;
	};

	let currentInputDevice = 'mouse'; // ('mouse' | 'touch')

	canvas.addEventListener('mousedown', (e) => {
		if (currentInputDevice !== 'mouse') return;
		onDrawStart(canvas, ctx, currentInputDevice, e);
		signing = true;
	});

	canvas.addEventListener('mousemove', (e) => {
		if (currentInputDevice !== 'mouse') return;
		onDrawContinue(canvas, ctx, currentInputDevice, e);
	});

	canvas.addEventListener('mouseup', (e) => {
		if (currentInputDevice !== 'mouse') return;
		onDrawEnd();
		signing = false;
	});

	canvas.addEventListener('mouseenter', (e) => {
		if (currentInputDevice !== 'mouse') return;
		if (signing)
			onDrawStart(canvas, ctx, currentInputDevice, e);
	});

	canvas.addEventListener('mouseout', (e) => {
		if (currentInputDevice !== 'mouse') return;
		onDrawEnd();
	});

	canvas.addEventListener('touchstart', (e) => {
		e.preventDefault();
		currentInputDevice = 'touch';
		onDrawStart(canvas, ctx, currentInputDevice, e);
	});

	canvas.addEventListener('touchmove', (e) => {
		onDrawContinue(canvas, ctx, currentInputDevice, e);
	});

	canvas.addEventListener('touchend', (e) => {
		currentInputDevice = 'mouse'; // reset to mouse
		onDrawEnd();
	});

	/**
	 * Clears any drawings on the canvas and resets the
	 * current path data.
	 * @returns {void}
	 */
	const clear = () => {
		ctx.clearRect(0, 0, canvas.width, canvas.height);
		currentPath = [];
	};

	/**
	 * Returns the current drawing path data if the user has drawn
	 * anything. The data format is an array of objects describing
	 * each point with the following keys:
	 *
	 * - x: x coordinate on canvas
	 * - y: y coordinate on canvas
	 * - lastX: previous x coordinate on canvas to draw from or undefined
	 * - lastY: previous y coordinate on canvas to draw from or undefined
	 *
	 * @returns {{x: number, y: number, lastX: (number|undefined), lastY: (number|undefined)}[]}
	 */
	const getPath = () => {
		return [...currentPath];
	};

	/**
	 * Sets the current drawing path data based on a previous path
	 * saved by calling `getPath`. If anything has been drawn on the
	 * canvas this will override it and re-draw the given path.
	 * Data should be in the same shape as the return from `getPath`.
	 * @param {{x: number, y: number, lastX: (number|undefined), lastY: (number|undefined)}[]} path
	 */
	const setPath = (path) => {
		clear();
		path.forEach((coord) => {
		draw(ctx, coord.lx, coord.ly, coord.mx, coord.my);
		});
		currentPath = [...path];
	};

	/**
	 * Returns `true` if anything has been drawn on the canvas by
	 * the user or by passing in a path with `setPath`.
	 * @returns {boolean}
	 */
	const hasPath = () => {
		return currentPath.length > 0;
	};

	/**
	 * Converts whatever has been drawn on the canvas to a base64 image
	 * data URL and returns it. Takes the same arguments as
	 * https://developer.mozilla.org/en-US/docs/Web/API/HTMLCanvasElement/toDataURL
	 * @param {string} type - Image encoding type
	 * @param {number} encoderOptions - Compression ratio from 0 - 1
	 */
	const toDataURL = (type = 'image/png', encoderOptions = 0.92) => {
		return canvas.toDataURL(type, encoderOptions);
	};

	return {clear, getPath, setPath, hasPath, toDataURL};
}
